<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <el-row>
                <el-col :span="24">
                     <el-button type="primary"></el-button>
                </el-col>
            </el-row>
            <el-table :data="rolesList" border style="width: 100%" stripe>
                <el-table-column type="expand">
                    <template slot-scope="scope">
                         <el-row v-for="(item1,i1) in scope.row.children" :key='i1' :class="['tagcenter','bdbottom',i1===0?'bdtop':'']">
                             <el-col :span="5">
                                 <el-tag>{{item1.authName}}</el-tag><i class="el-icon-caret-right"></i>
                             </el-col>
                             <el-col :span="19">
                                 <el-row v-for="(item2,i2) in item1.children" :key='i2' :class="['tagcenter',i2!==0?'bdtop':'']">
                                     <el-col :span="6">
                                         <el-tag type="info">{{item2.authName}}</el-tag><i class="el-icon-caret-right"></i>
                                     </el-col>
                                     <el-col :span="18">
                                         <el-tag type="warning" v-for="(item3,i3) in item2.children" :key='i3' closable @close='removeRightById(scope.row,item3.id)'>{{item3.authName}}</el-tag>
                                     </el-col>
                                 </el-row>
                             </el-col>
                         </el-row>
                    </template>
                </el-table-column>
                <el-table-column type="index"  label="#" width="50"></el-table-column>
                <el-table-column prop="roleName" label=""></el-table-column>
                <el-table-column prop="roleDesc" label="" ></el-table-column>
                <el-table-column prop="roleName" label="">
                    <template slot-scope="scope">
                            <el-button type="primary" icon="el-icon-edit"  size="small"></el-button>
                            <el-button type="danger" icon="el-icon-delete" size="small"></el-button>
                            <el-button type="info" icon="el-icon-setting"  size="small" @click="fenpeiPower(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="powerDialogVisible" width="30%" @close='closePowerDialogVisible'>
            <el-tree :props="props" :data="rightsList" node-key='id'  ref="tree" :default-expand-all=true show-checkbox :default-checked-keys='defleaves'>
            </el-tree>
            <span slot="footer" class="dialog-footer">
                <el-button @click="powerDialogVisible = false"> </el-button>
                <el-button type="primary" @click="saveRights"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    data(){
        return {
            rolesList:[],
            rightsList:[],
            powerDialogVisible:false,
             props: {
                        label: 'authName',
                        children: 'children'
                    },
            defleaves:[],
            roleId:''
        }
    },
    created(){
        this.initRoles();
    },
    methods:{
       async initRoles(){
           const {data:res}=await this.$http.get('/roles');
           if(res.meta.status!==200) return this.$message.error('');
           this.rolesList=res.data;
        }
        ,
       async removeRightById(role,id){
            const result=await this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                        }).catch(error=>error);
            if(result==='cancel') return this.$message.info('');
            //
            const {data:res}=await this.$http.delete(`roles/${role.id}/rights/${id}`);
            if(res.meta.status!==200) return this.$message.error("");
            this.$message.success("");
            role.children=res.data;
        },
        //
        async fenpeiPower(role){
            this.powerDialogVisible=true;
            const {data:res}=await this.$http.get('/rights/tree');
            if(res.meta.status!==200) return this.$message.error("");
            //

            role.children.forEach(item=>{
                this.getcheckLeaves(item,this.defleaves);
            });
            this.rightsList=res.data;
            this.roleId=role.id;
        },
        closePowerDialogVisible(){
            this.rightsList=[];
        },
        // ID
        getcheckLeaves(node,arr){
            if(!node.children){
               return arr.push(node.id);
            }
            else{
                node.children.forEach(item=>{
                    this.getcheckLeaves(item,arr);
                });
            }
        },
        //
        async saveRights(){
            const arr=[...this.$refs.tree.getCheckedKeys(),...this.$refs.tree.getHalfCheckedKeys()];
            const str=arr.join(',');
            const {data:res}=await this.$http.post(`roles/${this.roleId}/rights`,{rids:str});
            if(res.meta.status!==200) return this.$message.error('');
             this.powerDialogVisible=false;
            this.$message.success('');
            this.initRoles();
        }
    }
}
</script>
<style lang="less" scoped>
    .el-tag{
        margin: 7px;
    }
    .bdtop{
        border-top: 1px solid #eee
    }
    .bdbottom{
        border-bottom: 1px solid #eee;
    }
    .tagcenter{
        display: flex;
        align-content: center;
    }
</style>