<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row :gutter="20">
                <el-col :span="10"><el-input placeholder="" class="input-with-select"><el-button slot="append" icon="el-icon-search"></el-button></el-input></el-col>
                <el-col :span="6"></el-col>
            </el-row>
            <el-table :data="orderList"   border  style="width: 100%">
                <el-table-column  type="index" label="#" width="50"></el-table-column>
                <el-table-column  prop="order_number" label=""  width="250"> </el-table-column>
                <el-table-column  prop="order_price" label="" > </el-table-column>
                <el-table-column  prop="order_pay" label="" >
                    <template slot-scope="scope">
                        <el-tag type="warning" v-if='scope.row.order_pay===1'></el-tag>
                        <el-tag type="danger" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column  prop="is_send" label="" > </el-table-column>
                <el-table-column  prop="create_time" label="" >
                    <template slot-scope="scope">
                        {{scope.row.create_time|dataFormat}}                        
                    </template>    
                </el-table-column>
                <el-table-column  label="" > 
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" @click="showAddress" size="mini" circle></el-button>
                        <el-button type="success" icon="el-icon-location" @click="showProgress(scope.row)" size="mini" circle></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="param.pagenum"
                :page-sizes="[8, 15, 20, 50]"
                :page-size="param.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <el-dialog title="" :visible.sync="addressDialogVisible" width="50%"  >
            <span>
                <el-form ref="addressFromRef" :model="addressFrom" label-width="80px">
                    <el-form-item label="/">
                        <el-cascader
                            v-model="addressFrom.addr"
                            :options="addressData"  :props="{ expandTrigger: 'hover' }"></el-cascader>
                    </el-form-item>
                     <el-form-item label="">
                        <el-input v-model="addressFrom.consignee_addr"></el-input>
                    </el-form-item>
                </el-form>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="progressDialogVisible" width="50%"  >
            <span>
               <el-timeline :reverse='true'>
                    <el-timeline-item
                    v-for="(activity, index) in progressList"
                    :key="index"
                    :timestamp="activity.time">
                    {{activity.context}}
                    </el-timeline-item>
                </el-timeline>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="progressDialogVisible = false"> </el-button>
                <el-button type="primary" @click="progressDialogVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import addressData from '../../citydata.js'
export default {
    data(){
        return {
            orderList:[],
            param:{
                query:'',
                pagenum:1,
                pagesize:8
            },
            total:0,
            addressDialogVisible:false,
            addressData,
            addressFrom:{},
            progressDialogVisible:false,
            progressList:[]
       

        }
    },
    created(){
        this.getOrderList();
    },
    methods:{
        async getOrderList(){
            const {data:res}=await this.$http.get('/orders',{params:this.param});
            if(res.meta.status !==200) return this.$message.error("");
            this.orderList=res.data.goods;
            console.log(res);
            this.total=res.data.total;
        },
        handleSizeChange(v){
            this.param.pagesize=v;
            this.getOrderList();
        },
        handleCurrentChange(v){
            this.param.pagenum=v;
            this.getOrderList();
        },
        showAddress(){
            this.addressDialogVisible=true;
        },
        async showProgress(order){
            this.progressDialogVisible=true;
            this.progressList=this.progress.data;
        }
    },
    computed:{
        progress(){
            return {
                "data": [
                    {
                    "time": "2018-05-10 09:39:00",
                    "ftime": "2018-05-10 09:39:00",
                    "context": ",,",
                    "location": ""
                    },
                    {
                    "time": "2018-05-10 08:23:00",
                    "ftime": "2018-05-10 08:23:00",
                    "context": "[]  95338",
                    "location": ""
                    },
                    {
                    "time": "2018-05-10 07:32:00",
                    "ftime": "2018-05-10 07:32:00",
                    "context": " []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-10 02:03:00",
                    "ftime": "2018-05-10 02:03:00",
                    "context": "[], []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 23:05:00",
                    "ftime": "2018-05-09 23:05:00",
                    "context": " []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 21:21:00",
                    "ftime": "2018-05-09 21:21:00",
                    "context": "[], []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 13:07:00",
                    "ftime": "2018-05-09 13:07:00",
                    "context": " ",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 12:25:03",
                    "ftime": "2018-05-09 12:25:03",
                    "context": "",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 12:22:24",
                    "ftime": "2018-05-09 12:22:24",
                    "context": "HLA",
                    "location": ""
                    },
                    {
                    "time": "2018-05-08 21:36:04",
                    "ftime": "2018-05-08 21:36:04",
                    "context": "",
                    "location": ""
                    }
                ],
                "meta": { "status": 200, "message": "" }
                }
        }
    }

}
</script>
<style lang="less" scoped>
    .el-card{
        height: 100%;
    }
    .el-cascader{
        width: 100%;
    }
</style>