<template>
    <div>
        <el-breadcrumb >
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <el-card >
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" v-model="params.query" class="input-with-select" clearable @clear='initUsers'>
                    <el-button slot="append" @click="initUsers" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addUserDialogVisible"></el-button>
                </el-col>               
            </el-row>
             <el-table  :data="userList" border style="width: 100%">
                    <el-table-column type="index" label="#"> </el-table-column>
                    <el-table-column  prop="username" label=""> </el-table-column>
                    <el-table-column  prop="email" label="" > </el-table-column>
                    <el-table-column  prop="mobile" label="" > </el-table-column>
                    <el-table-column  prop="role_name" label="" > </el-table-column>
                    <el-table-column  prop="mg_state" label=""> 
                        <template slot-scope="scope">
                            <el-switch v-model="scope.row.mg_state" @change="changeState(scope.row)"></el-switch>
                        </template>
                        
                    </el-table-column>
                    <el-table-column   label="" >
                        <template slot-scope="scope">
                        <el-tooltip class="item" effect="dark" content=""  placement="top" :enterable=false>
                            
                            <el-button type="primary" icon="el-icon-edit" circle size="mini" @click="editUserDialogVisible(scope.row.id)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top" :enterable=false>
                            <el-button type="primary" icon="el-icon-delete" circle size="mini" @click="removeUserById(scope.row.id)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top" :enterable=false>
                            <el-button type="primary" icon="el-icon-setting" circle size="mini" @click="fenpeiRole(scope.row)"></el-button>
                        </el-tooltip>
                        </template>
                    </el-table-column>
             </el-table>
             <!--  -->
                 <el-pagination  @size-change="handleSizeChange" @current-change="handleCurrentChange"  :current-page="params.pagenum" :page-sizes="[1, 2, 4, 10]"
                                :page-size="params.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
            <!--  -->
            <el-dialog title="" :visible.sync="dialogVisible"  width="30%" @close='addDialogVisible'>
                    <el-form :model="userForm" :rules="userFormRules" ref="userFormRef"  label-width="80px">
                        <el-form-item label="" prop='username'>
                            <el-input v-model="userForm.username"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='password'>
                            <el-input v-model="userForm.password"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='email'>
                            <el-input v-model="userForm.email"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='mobile'>
                            <el-input v-model="userForm.mobile"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false"> </el-button>
                        <el-button type="primary" @click="addUser"> </el-button>
                    </span>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close='resetFiled'>
                     <el-form :model="editForm" :rules="editFormRules" ref="editFormRef"  label-width="80px">
                        <el-form-item label="" prop='username'>
                            <el-input v-model="editForm.username" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='email'>
                            <el-input v-model="editForm.email"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='mobile'>
                            <el-input v-model="editForm.mobile"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="editDialogVisible = false"> </el-button>
                        <el-button type="primary" @click="submitEdit"> </el-button>
                    </span>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="roleDialogVisible" width="30%" @close='clearRole'>
                <span><p>:{{userinfo.username}}</p></span>
                <span><p>:{{userinfo.role_name}}</p></span>
                 <el-select v-model="role" placeholder="">
                    <el-option  v-for="item in roleList"  :key="item.id"  :label="item.roleName" :value="item.id"> </el-option>
                </el-select>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="roleDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="saveRoleById(userinfo.id)"> </el-button>
                </span>
                </el-dialog>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        var checkEamil=(rule,value,callback)=>{
            const emailRex=/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
            if(emailRex.test(value))
            {
                return callback();
            }
            return callback(new Error(''));
        }
        var checkMobile=(rule,value,callback)=>{
            const mobileRex=/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
            if(mobileRex.test(value))
            {
                return callback();
            }
            return callback(new Error(''))
        }
        return {
            params:{query:'',pagenum:1,pagesize:4},
            userList:[],
            total:0,
            dialogVisible:false,
            editDialogVisible:false,
            userForm:{
                username:'',
                password:'',
                email:'',
                mobile:''
            },
            userFormRules:{
                username:[
                     { required: true, message: '', trigger: 'blur' },
                     { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
                ],
                password:[
                     { required: true, message: '', trigger: 'blur' },
                ],
                email:[
                    { required: true, message: '', trigger: 'blur' },
                    {
                        validator:checkEamil,trigger: 'blur'
                    }
                ],
                mobile:[
                     { required: true, message: '', trigger: 'blur' },
                     {validator:checkMobile,trigger: 'blur'}
                ]
            }
            ,
            editForm:{
                
            },
            editFormRules:{
                email:[{required: true, message: '', trigger: 'blur' },{validator:checkEamil,trigger: 'blur'}],
                mobile:[ { required: true, message: '', trigger: 'blur' },
                     {validator:checkMobile,trigger: 'blur'}]
            },
            roleDialogVisible:false,
            userinfo:{},
            roleList:[],
            role:''
        }
    },
    created(){
        this.initUsers();
    },
    methods:{
        initUsers:async function(){
            const {data:res}=await this.$http.get('/users',{params:this.params});
            this.userList=res.data.users;
            this.total=res.data.total;
        },
        handleSizeChange:function(value){
            this.params.pagesize=value;
            this.initUsers();
        },
        handleCurrentChange:function(value)
        {
             this.params.pagenum=value;
             this.initUsers();
        },
        //
        changeState: async function(userinfo){
            const {data:res}= await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`);
            if(res.meta.status!==200){
                userinfo.mg_state=!userinfo.mg_state;
                return this.$message.error('');
            } 
        },
        addUserDialogVisible:function(){
            this.dialogVisible=true;
        },
        addDialogVisible(){
            this.$refs.userFormRef.resetFields()
        },
        //
        addUser(){
            this.$refs.userFormRef.validate(async valid=>{
                if(!valid){
                    return;
                }
                const {data:res}=await this.$http.post('users',this.userForm);
                if(res.meta.status!==201) return this.$message.error('');
                this.$message.success('');
                this.dialogVisible=false;
            });
        },
        //
        async editUserDialogVisible(id){
            this.editDialogVisible=true
            const {data:res} =await this.$http.get('users/'+id);
            if(res.meta.status!=200) return this.$message.error("");
            this.editForm=res.data;
        },
        resetFiled(){
            this.$refs.editFormRef.resetFields();          
        },
        submitEdit(){
            this.$refs.editFormRef.validate(async valid=>{
                if(!valid) return;
                //
                const {data:res}=await this.$http.put('/users/'+this.editForm.id,{email:this.editForm.email,mobile:this.editForm.mobile});
                if(res.meta.status!==200) return this.$message.error('');
                this.editDialogVisible=false;
                this.$message.success('');
                this.initUsers();
            });
        },
        //
        async removeUserById(id){
            const result=await  this.$confirm('?', '', {
                                confirmButtonText: '',
                                cancelButtonText: '',
                                type: 'warning'
                         }).catch(err=> err);
                         console.log(result);
                         if(result==='cancel') return this.$message.info('');
            const {data:res}=await this.$http.delete('users/'+id);
            if(res.meta.status!==200) return this.$message.error('');
            this.$message.success('');
            this.initUsers();
        },
        //
        async  fenpeiRole(user)
        {
            this.userinfo=user;
            //
            const {data:res}=await this.$http.get('/roles');
            if(res.meta.status!==200) return this.$message.error('');
            this.roleList=res.data;
            this.roleDialogVisible=true;
        },
        clearRole(){
            this.roleList=[];
            this.userinfo={};
            this.role='';
        },
        async saveRoleById(id){
            if(!this.role)
            {
                return this.$message.error('');
            }
            const {data:res}= await this.$http.put(`users/${id}:id/role`,{rid:this.role});
            if(res.meta.status!==200) return this.$message.error('');
            this.roleDialogVisible=false;
            this.$message.success('');    
            this.initUsers();
        }
    }
   
}
</script>
<style lang="less" scoped>

</style> 